/* add css module styles here (optional) */

section {
  padding: 14px 12px;
}

header {
  font-weight: bold;
  padding-bottom: 12px;
  border-bottom: 1px solid #EAECEF;
  margin-bottom: 10px;
}

nav {
  margin-bottom: 18px;
  display: flex;
  justify-content: space-between;
}

nav ._3sodH {
  background-color: #F2F2F2;
  font-weight: bold;
  border: none;
  outline: none;
}

nav button {
  background: transparent;
  border: none;
  box-sizing: border-box;
  margin: 0;
  min-width: 0;
  position: relative;
  white-space: nowrap;
  color: #76808F;
  font-size: 14px;
  /* margin: 0 4px; */
}
nav button:first-child {
  width: 25px;
  margin-right: 8px;
}
nav button:last-child {
  margin-right: 0;
}
aside {
  margin-bottom: 4px;
  display: flex;
  justify-content: space-between;
}

input[type="text"] {
  margin-right: 8px;
  border: none;
  border-bottom: 1px solid #EAECEF;
}

/* input[type="radio"] {

} */

label {
  font-size: 12px;
}

._FnYjQ header {
  box-sizing: border-box;
  margin: 0;
  min-width: 0;
  display: flex;
  padding-left: 0;
  padding-right: 16px;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid;
  border-color: #EAECEF;
  padding-top: 16px;
  padding-bottom: 16px;
  /* background-color: #FAFAFA; */
}

._FnYjQ  header a {
  box-sizing: border-box;
  margin: 0;
  min-width: 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 12px;
  color: #76808F;
  text-align: left;
  justify-content: flex-start;
  text-decoration: none;
}

._FnYjQ  header svg {
  width: 1.6em;
  height: 1.6em;
  display: inline-block;
  vertical-align: top;
}

._FnYjQ ._3sodH svg {
  color: #f0b90b;
}

._FnYjQ  ul {
  margin: 0;
  padding: 0;
}

._FnYjQ  ul li div {
  width: 30%;
  display: inline-block;
}

._FnYjQ  ul li div svg {
  width: 1em;
  color: #76808F;
  cursor: pointer;
}

._FnYjQ  ul li div:first-child {
  width: 10%;
  text-align: center;
}

._FnYjQ  ul li div:last-child {
  text-align: right;
}

._3ybTi {
  margin: 2em;
  padding: 0.5em;
  border: 2px solid #000;
  font-size: 2em;
  text-align: center;
}